import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { fuseAnimations } from '@fuse/animations';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { IFondo } from 'app/modules/fondos/fondos/models/fondo';
import { ITipoMovimiento } from 'app/modules/fondos/tipo-movimientos/models/tipo-movimiento';
import { MovimientosService } from '../../service/movimientos.service';
import { IMovimiento } from '../../models/movimiento';
import { Subject, takeUntil } from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
  selector: 'app-filter-menu-movimientos',
  templateUrl: './filter-menu.component.html',
  styleUrls: ['./filter-menu.component.scss'],
  animations: fuseAnimations
})
export class FilterMenuComponent implements OnInit {
  tipos_movimientos: ITipoMovimiento[] = [];
  fondos: any;
  public formFilters: FormGroup;
  public proveedorespp: any[];
  color_primario: string;
  color_secundario: string;
  default_color: string = 'bg-primary';
  private _unsubscribeAll: Subject<any> = new Subject<any>();
  
  constructor(
    @Inject('MENU_DATA') public data: any, 
    private menuService: JedsMenuService, 
    private _movimientosService: MovimientosService,
    private _empresaService: EmpresaService
  ) { }

  ngOnInit(): void {
    // Subscribe to empresa data
    this._empresaService.empresa$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe(empresa => {
          this.color_primario = empresa.color_primario;
          this.color_secundario = empresa.color_secundario;
      });

    // Load empresa data
    this._empresaService.getEmpresa();
    this.getFondos();
    this.getTiposMovimientos();

    /**
    * Formulario de filtros
    */
    this.formFilters = new FormGroup({
      fecha: new FormControl(('')),
      bonificado: new FormControl(('')),
      idtipo: new FormControl(('')),
      origen: new FormControl(('')),
      destino: new FormControl(('')),
    });
    this.formFilters.patchValue(this.data.filter);
  }

  resetFilters() {
    this.formFilters.reset({
      bonificado: -1,
      idtipo: -1,
      origen: -1,
      destino: -1,
    });
  }

  onCloseMenu(data: any = null) {
    this.menuService.close(data);
  }

  /**
   * Esta funcion es para listas los fondos de origen y destino en el componente del filtro.
   * @returns fondos de origen y destino
   */
  getFondos() {
    const fondos = this._movimientosService.getFondos(true).subscribe(fondos => { this.fondos = fondos; });
    return fondos;
  }

  /**
   * Esta funcion es para listar los tipos de movimientos en el filtro de la grilla
   * @returns tipos de movimientos
   */
  getTiposMovimientos() {
    const tipos_movimientos = this._movimientosService.getTiposMovimientos().subscribe(tipos_movimientos => { this.tipos_movimientos = tipos_movimientos; })
    return tipos_movimientos;
  }
}