import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { IMovimiento } from '../../models/movimiento';
import { MovimientosService } from '../../service/movimientos.service';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { ITipoMovimiento } from 'app/modules/fondos/tipo-movimientos/models/tipo-movimiento';
import { IFondo } from 'app/modules/fondos/fondos/models/fondo';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { IAlertConfig } from '@shared/models/alert';
import { finalize, Subject, takeUntil, takeWhile, tap, timer } from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { DatePipe } from '@angular/common';

@Component({
    selector: 'app-save-movimiento',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
    providers: [
        { provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS },
    ]
})
export class SaveComponent implements OnInit {
    columns = ['fecha', 'tipo', 'origen', 'destino', 'importe', 'descripcion', 'bonificado']
    form: FormGroup;
    movimiento: IMovimiento;
    title: string;
    fondos: IFondo[] = [];
    tipos_movimientos: ITipoMovimiento[] = [];
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();
    @Output() public creadoExitosamente: EventEmitter<void> = new EventEmitter<void>();
    @Output() public editadoExitosamente: EventEmitter<void> = new EventEmitter<void>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private movimientosService: MovimientosService,
        private _empresaService: EmpresaService,
        public alert: AppAlertService,
        public menu: JedsMenuService,
        public fb: FormBuilder,
        public dialogRef: MatDialogRef<SaveComponent>,
        private datePipe: DatePipe
    ) {
        this.movimiento = this.data ? this.data : null;
        this.title = this.movimiento?.idmovimiento ? 'Editar Movimiento' : 'Nuevo Movimiento';
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
        
        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
        this.exist();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
          next: (config: IAlertConfig) => {
            this.showAlert = true;
            this._alertConfig = {
              ...config,
              appearance: 'border',
            };
            let time = 2;
            timer(100, 1500)
              .pipe(
                finalize(() => {
                  this.showAlert = false;
                  this._alertConfig = null;
                }),
                takeWhile(() => time > 0),
                takeUntil(this._unsubscribeAll),
                tap(() => (time -= 1))
              )
              .subscribe();
          },
        });
    }

    newForm() {
        const [dia, mes, anio] = this.movimiento ? this.movimiento.fecha.split('/') : '';
        this.form = this.fb.group({
            fecha: new FormControl((this.movimiento?.fecha ? this.datePipe.transform(new Date(`${mes}/${dia}/${anio}`), 'yyyy-MM-dd') : this.datePipe.transform(new Date(), 'yyyy-MM-dd')), [Validators.required]),
            bonificado: new FormControl((this.movimiento ? this.movimiento.bonificado : '')),
            tipo: new FormControl((this.movimiento ? this.movimiento.idtipo : ''), [Validators.required]),
            origen: new FormControl((this.movimiento ? this.movimiento.origen : ''),),
            destino: new FormControl((this.movimiento ? this.movimiento.destino : '')),
            importe: new FormControl((this.movimiento ? this.movimiento.importe : ''), [Validators.required]),
            descripcion: new FormControl((this.movimiento ? this.movimiento.descripcion : ''), [Validators.required]),
        });
    }

    /**
     * Si existe un movimiento le mando 'true' a fondos y nada a TM para que me traiga todos los fondos y TMs al editar.
     * Caso contrario, si se va a crear un movimiento, llamo a los fondos sin enviarle un parametro asi me trae solo los activos y
     * le envio un paramtro a los tipos de movimientos para que me traiga los activos.
     */
    exist(){
        if(this.movimiento?.idmovimiento){
            this.getFondos(true);
            this.getTiposMovimientos()
        }else{
            this.getFondos();
            this.getTiposMovimientos(true);
        }
    }

    /**
     * Esta funcion la utilizamos cuando necesitamos listar los fondos de origen y destino en el formulario
     */
    getFondos(option?) {
        const fondos = this.movimientosService.getFondos(option).subscribe(fondos => { this.fondos = fondos; })
        return fondos;
    }

    /**
     * Esta funcion la utilizamos cuando necesitamos listar los tipos de movimientos en el formulario 
     */
    getTiposMovimientos(option?) {
        const tipos_movimientos = this.movimientosService.getTiposMovimientos(option).subscribe(tipos_movimientos => { this.tipos_movimientos = tipos_movimientos; })
        return tipos_movimientos;
    }


    onSubmit() {
        if (!this.form.valid) {
            return this.alert.error('Revise Los Datos Ingresados');
        } else {
            this.movimiento = {
                ...this.movimiento,
                fecha: this.form.get('fecha').value,
                bonificado: this.form.get('bonificado').value ? 1 : 0,
                idtipo: this.form.get('tipo').value,
                origen: this.form.get('origen').value,
                destino: this.form.get('destino').value,
                importe: this.form.get('importe').value,
                descripcion: this.form.get('descripcion').value,
            }
            if (this.movimiento.idmovimiento) {
                this.movimientosService.updateMovimiento(this.movimiento)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.editadoExitosamente.emit();
                                this.onCloseMenu();
                            }
                        }
                    });
            } else {
                this.movimientosService.saveMovimiento(this.movimiento)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.creadoExitosamente.emit();
                                this.onCloseMenu()
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}